@import "https://fonts.googleapis.com/css2?family=Assistant:wght@200..800&display=swap";
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  color: #070040;
  font-family: Assistant, Arial, sans-serif;
  font-weight: 500;
  line-height: 2rem;
}

header {
  z-index: 20;
  color: #fff;
  background-color: #10a5f5;
  align-items: center;
  height: auto;
  min-height: 130px;
  font-weight: 700;
  display: grid;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.logo {
  grid-column: 1 / 2;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 1.5rem;
}

.logo > a > img {
  max-width: 100px;
  max-height: 100px;
}

@media (width >= 500px) {
  .logo > a > img {
    max-width: 120px;
    max-height: 120px;
  }
}

.menu {
  grid-column: 2 / 2;
  align-items: center;
  font-size: .6rem;
}

@media (width >= 500px) {
  .menu {
    justify-content: flex-end;
    font-size: .8rem;
    display: flex;
  }
}

.encabezado {
  width: 100%;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

@media (width >= 500px) {
  .encabezado {
    width: 100%;
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
    display: grid;
  }
}

@media (width >= 800px) {
  .encabezado {
    width: 100%;
    max-width: 70%;
    margin-left: auto;
    margin-right: auto;
    display: grid;
  }
}

nav {
  text-transform: uppercase;
  font-weight: 700;
}

nav > ul {
  list-style-type: none;
}

nav > ul > li {
  padding-left: .5rem;
  padding-right: .5rem;
  display: inline-block;
}

footer {
