  }
}

.mapa-del-sitio ul {
  padding: 0;
  list-style: none;
  position: relative;
}

.mapa-del-sitio li:hover ul {
  opacity: 1;
  visibility: visible;
}

.mapa-del-sitio li ul {
  opacity: 0;
  visibility: hidden;
  margin: 0;
  padding: 0;
  line-height: 1.7rem;
  transition: opacity .3s, transform .3s;
  position: absolute;
  top: -35%;
  left: 30%;
}

.mapa-del-sitio li ul li {
  color: #070040;
  opacity: 0;
  background-color: #fff;
  padding: .2rem;
  transition: opacity .3s, transform .3s;
}

.mapa-del-sitio li:hover ul li {
  opacity: 1;
  color: #070040;
}

.mapa-del-sitio li:hover ul li:first-child {
  transition-delay: .1s;
}

.mapa-del-sitio li:hover ul li:nth-child(2) {
  transition-delay: .2s;
}

.mapa-del-sitio li:hover ul li:nth-child(3) {
  transition-delay: .3s;
}

.mapa-del-sitio li:hover ul li:nth-child(4) {
  transition-delay: .4s;
}

.mapa-del-sitio li:hover ul li:nth-child(5) {
  transition-delay: .5s;
}

.mapa-del-sitio li:hover ul li:nth-child(6) {
  transition-delay: .6s;
}

.mapa-del-sitio a {
  color: inherit;
  text-decoration: none;
}

.mapa-del-sitio a:hover {
  color: #10a5f5;
  text-decoration: underline;
