@keyframes fondoMueve {
  0% {
    transform: translateX(-100%);
  }

  40% {
    transform: translateX(40vw);
  }

  50% {
    transform: translate(40vw, -20%);
  }

  60% {
    transform: translate(40vw);
  }

  70% {
    transform: translate(40vw, -10%);
  }

  75% {
    transform: translate(40vw);
  }

  100% {
    transform: translate(100vw);
  }
}

.fondoMueve {
  fill: #10a5f5;
  width: 20vw;
  height: 20vw;
  animation: 5s ease-in infinite forwards fondoMueve;
}

